body, p {
  margin: 0;
}

.card__button {
  text-decoration: none;
}

.card {
  box-sizing: border-box;
  border: 1px solid #fff;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__product {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__description {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rewiews {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

